<template>
  <div class="login">
    <div class="login_box">
        <!-- 登录 -->
        <div style="width:100%;height:50px;line-height:50px;text-align:center;background-color:#909399;color:white;border-radius:10px 10px 0 0">登录</div>
        <div style="padding:20px"> 
            <div style="text-align:center">账号：<el-input  placeholder="请输入账号" style="width:400px" v-model="account"></el-input></div>
            <div style="margin-top:10px;text-align:center">密码：<el-input placeholder="请输入密码" v-model="password" show-password style="width:400px"></el-input></div>
            <div style="text-align:center;margin-top:50px"><el-button type="primary" style="width:200px;" @click="goIndex">登录</el-button></div>
        </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Login',
  data () {
    return {
      account:'',
      password:''
    }
  },
  methods:{
      //登录，前往首页
      goIndex(){
          //账号不能为空
          if (!this.account) {
            this.$message({
                message: '账号不能为空',
                type: 'warning'
            });
          }
          //密码不能为空
          if (!this.password) {
              this.$message({
                message: '密码不能为空',
                type: 'warning'
            });
          }
          //登录
          if (this.account&&this.password) {
              this.$router.push({
                  path:'/'
              })
          }
      }
  },
  mounted(){
      
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.login_box{
    margin: 150px auto;
    width: 500px;
    border: 1px solid #DCDFE6;
    border-radius: 10px
}
</style>


